import * as type from "../constants/ActionType";
var data = JSON.parse(localStorage.getItem("TODO"));

var initialState = data ? data : [];

var findIndex = (todos, id) => {
  var result = -1;
  todos.forEach((item, index) => {
    if (item.id === id) {
      result = index;
    }
  });
  return result;
};

const todos = (state = initialState, action) => {
  var id = action.id;
  var index = findIndex(state, id);

  switch (action.type) {
    case type.ADD_TODO:
      var temp = state;
      temp.sort((a, b) => {
        return a.id - b.id;
      });

      var newTodo = {
        id: temp[0] === undefined ? 1 : temp[temp.length - 1].id + 1,
        title: action.item,
        check: false,
        delete: false,
        mark: false,
      };
      state.push(newTodo);
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.CHECKED:
      state[index].check = !state[index].check;
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.CHECK_ALL:
      state.forEach((item) => {
        if (item.check === false) {
          item.check = true;
        }
      });
      state = state.filter((item) => item.check !== true);
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.CLEAR_FINISHED:
      if (state[index].check === true) {
        state.splice(index, 1);
      }
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.EDIT_TODO:
      state[index].title = action.title;
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.DEL_TODO:
      state.splice(index, 1);
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.DELETE_ALL:
      state = state.filter((item) => item.delete !== false);
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];

    case type.MARKED:
      state[index].mark = !state[index].mark;
      state = state.sort((a, b) => {
        return b.mark - a.mark;
      });
      localStorage.setItem("TODO", JSON.stringify(state));
      return [...state];
    default:
      return [...state];
  }
};

export default todos;
