import React, { Component } from "react";
import { connect } from "react-redux";
import { checkAll, deleteAll } from "../actions";

class Option extends Component {
  render() {
    var { todos } = this.props;
    var { onCheckAll } = this.props;
    var { onDeleteAll } = this.props;
    return (
      <div className="footer mt-5 border rounded d-flex justify-content-between">
        <div className="footer__checkall">
          <a
            className="footer-check"
            onClick={() => {
              onCheckAll();
            }}
          >
            Chọn tất cả
          </a>
        </div>
        <div className="footer-text">{this.showAllItem(todos)}</div>
        <div className="footer__deleteall float-right">
          <a className="footer-del" onClick={() => onDeleteAll()}>
            Xoá tất cả
          </a>
        </div>
      </div>
    );
  }

  onDeleteAll = () => {
    this.props.onDeleteAll();
  };

  onCheckAll = () => {
    this.props.onCheckAll();
  };

  showAllItem = (todos) => {
    return (
      <p className="d-inline mt-1">{`${todos.length} công việc chưa làm`}</p>
    );
  };
}
const mapStateToProps = (state) => {
  return {
    todos: state.todos,
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
    onCheckAll: () => {
      dispatch(checkAll());
    },
    onDeleteAll: () => {
      dispatch(deleteAll());
    },
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Option);
