import React, { Component } from "react";
import { connect } from "react-redux";
import TodoItem from "../components/TodoItem";
import TodoList from "../components/TodoList";
import {
  actChecked,
  actMarked,
  clearFinished,
  delTodo,
  editTodo,
} from "../actions/index";
class TodoContainer extends Component {
  render() {
    var { todos } = this.props;

    return <TodoList> {this.showTodoItem(todos)}</TodoList>;
  }

  showTodoItem(todos) {
    var result = null;
    var { onChecked } = this.props;
    var { onMarked } = this.props;
    var { onDelTodo } = this.props;
    var { onEditTodo } = this.props;
    var { onFinished } = this.props;
    if (todos.length > 0) {
      result = todos.map((item, index) => {
        return (
          <TodoItem
            key={index}
            item={item}
            onChecked={onChecked}
            onMarked={onMarked}
            onDelTodo={onDelTodo}
            onEditTodo={onEditTodo}
            onFinished={onFinished}
          />
        );
      });
    }
    return result;
  }
}

const mapStateToProps = (state) => {
  return {
    todos: state.todos,
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
    onChecked: (id) => {
      dispatch(actChecked(id));
    },
    onMarked: (id, mark) => {
      dispatch(actMarked(id, mark));
    },
    onDelTodo: (id) => {
      dispatch(delTodo(id));
    },
    onEditTodo: (id, title) => {
      dispatch(editTodo(id, title));
    },
    onFinished: (id, check) => {
      setTimeout(() => {
        dispatch(clearFinished(id, check));
      }, 3000);
    },
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(TodoContainer);
