import * as type from "../constants/ActionType";

export const actChecked = (id) => {
  return {
    type: type.CHECKED,
    id,
  };
};

export const actMarked = (id, mark) => {
  return {
    type: type.MARKED,
    id,
    mark,
  };
};

export const addTodo = (item) => {
  return {
    type: type.ADD_TODO,
    item,
  };
};

export const delTodo = (id) => {
  return {
    type: type.DEL_TODO,
    id,
  };
};

export const editTodo = (id, title) => {
  return {
    type: type.EDIT_TODO,
    id,
    title,
  };
};
export const clearFinished = (id, check) => {
  return {
    type: type.CLEAR_FINISHED,
    id,
    check,
  };
};

export const checkAll = () => {
  return {
    type: type.CHECK_ALL,
  };
};

export const deleteAll = () => {
  return {
    type: type.DELETE_ALL,
  };
};
