import React, { Component } from "react";

class TodoItem extends Component {
  render() {
    var { item } = this.props;
    return (
      <li className="list-group-item">
        <a
          onClick={() => this.onChecked(item.id)}
          onMouseUp={() => this.onFinished(item.id, item.check)}
        >
          {this.showChecked(item)}
        </a>
        <input
          id="todo-item"
          type="text"
          className={item.check ? `form-control checked` : "form-control"}
          value={item.title}
          name="title"
          onChange={this.onChange}
          onKeyDown={() => {
            this.onEditTodo(item.id, item.title);
          }}
        />
        <span className="form-inline float-right">
          <a
            className="markstar mr-4"
            onClick={() => this.onMarked(item.id, item.mark)}
          >
            {this.showMarked(item)}
          </a>
          <button
            onClick={() => this.onDelTodo(item.id)}
            className="btn btn-danger btn-del"
          >
            Xóa
          </button>
        </span>
      </li>
    );
  }

  onFinished = (id, check) => {
    this.props.onFinished(id, check);
  };
  onEditTodo = (id, title) => {
    this.props.onEditTodo(id, title);
  };

  onChange = (e) => {
    this.onEditTodo(this.props.item.id, e.target.value);
  };

  onDelTodo = (id) => {
    this.props.onDelTodo(id);
  };
  onChecked = (id) => {
    this.props.onChecked(id);
  };

  onMarked = (id, mark) => {
    this.props.onMarked(id, mark);
  };

  showChecked(item) {
    if (item.check === true) {
      return <i className="fas fa-check-circle pr-4"></i>;
    } else {
      return <i className="far fa-circle pr-4"></i>;
    }
  }

  showMarked(item) {
    if (item.mark === true) {
      return <i className="fas fa-star"></i>;
    } else {
      return <i className="far fa-star"></i>;
    }
  }
}

export default TodoItem;
